class Land_Florafauna extends Component{


  state = {
    sideDrawerOpen:false
  };

drawerToggleClickHandler = ()=> {

  this.setState((prevState) =>{
    return{sideDrawerOpen: !prevState.sideDrawerOpen};
});
};

backdropClickHandler = () => {
    this.setState({sideDrawerOpen:false});
};

  render(){

   
    let backdrop;

    if (this.state.sideDrawerOpen){
   
      backdrop= <Backdrop click={this.backdropClickHandler}/>
    }
    return(
      
        <body> 
          <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
          <SideDrawer show={this.state.sideDrawerOpen} />
          {backdrop}
          <SideMenuLand />
          <LandBg/>
          
          <main className = "bod">

                    <Stacker>
            
      <Contentwrapper>
          <Left>
           <Threat>  
           <Contentwrapper>
          <Tree><Link to="/Land"><Backico src= {back}/></Link><Link to="/Land" className="link">Land </Link>/ Temperature Rise</Tree>
          </Contentwrapper>        
              <CardTemperaturerise/>
           </Threat>
          </Left>
      </Contentwrapper>
     
      <Contentwrapper>
          <Right>
           <Impacts>
            <Impactwrapper><Link to= "/Land_Forestfires"><InfoForestfires/></Link></Impactwrapper>
            <Impactwrapper><Link to= "/Land"><InfoArabica/></Link></Impactwrapper>
            <Impactwrapper><Link to= "/Land"><InfoHoneybee/></Link></Impactwrapper>
            <Impactwrapper><Link to= "/Land"><InfoBanana/></Link></Impactwrapper>
            <Impactwrapper><Link to= "/Oceans_Warming"><InfoOceanwarming/></Link></Impactwrapper>
            
            
            <Contentwrapper><Title>Take Action</Title></Contentwrapper>  
            <Impactwrapper><Link to= "/Take-Action"><InfoKeepcool/></Link></Impactwrapper>
           </Impacts>
          </Right> 
      </Contentwrapper>

      </Stacker>

          

         
                  
          </main>
        </body>
      
      );

  } 
}

export default Land_Florafauna;